import React, { useState, useEffect } from "react";
import { Row, Col, Input } from "antd";
import { withRouter } from "react-router-dom";
import queryString from "query-string";
import MovieCatalog from "../../Components/MovieCatalogo";
import Footer from "../../Components/Footer";
import { URL_API, API } from "../../utils/constants";

import "./seach.scss";

function Search(props) {
  const { location, history } = props;
  const [movieList, setMovieList] = useState([]);
  const [searchValue, setSearchValue] = useState([]);

  useEffect(() => {
    (async () => {
      const searchValue = queryString.parseUrl(location.search);
      const { s } = searchValue.query;
      const response = await fetch(
        `${URL_API}/search/movie?api_key=${API}&lenguage=es-ES&query=${s}&page=1`
      );

      const movies = await response.json();

      setSearchValue(s);
      setMovieList(movies);
    })();
  }, [location.search]);

  const changeSearch = (e) => {
    const urlParams = queryString.parse(location.search);
    urlParams.s = e.target.value;
    history.push(`?${queryString.stringify(urlParams)}`);
    setSearchValue(e.target.value);
  };

  return (
    <Row>
      <Col span="12" offset="6" className="seach">
        <h1>Busca tu pelicula</h1>
        <Input value={searchValue} onChange={changeSearch} />
      </Col>
      {movieList.results && (
        <div className="seachGrid">
          <MovieCatalog movie={movieList} />
        </div>
      )}
      <Col span="24">
        <Footer />
      </Col>
    </Row>
  );
}

export default withRouter(Search);
